import React from "react"
import { Link, graphql } from "gatsby"

import Bio from "../components/bio"
import Jobs from "../components/jobs"
import Layout from "../components/layout"
import SEO from "../components/seo"
import { rhythm } from "../utils/typography"
import "../components/utility.css"
import ProjectCard from "../components/projectCard"

class BlogIndex extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      showJobs: false,
      className: "hide",
    }
    this.toggleJobs = this.toggleJobs.bind(this)
  }

  toggleJobs() {
    // this.setState(prevState => ({
    //   showJobs: !prevState.showJobs
    // }));
    if (this.state.className === "show") {
      this.setState({ className: "hide", showJobs: false })
    } else {
      this.setState({ className: "show", showJobs: true })
    }
  }

  render() {
    const { data } = this.props
    const siteTitle = data.site.siteMetadata.title
    const posts = data.allMarkdownRemark.edges
    const { showJobs, className } = this.state

    const financeDescription =
      "A small web app for financial calculations. This is still a work in progress and I am in the process of adding account management and a secured backend. This app uses the MERN stack. I currently have a backend that is not yet deployed that uses jwt for route protection and bcrypt for password hashing/salting. I recognize that a truly secure system needs additional levels of authentication and redundancy, however this is being made for learning purposes. For my graphs I am using ChartJS with the React wrapper library react-chartjs-2. Getting the graph to render properly, especially based on form state was actually a bit of a challenge."

    const surv =
      "This was a website that I made for a client using html/css/js and Bootstrap. I was not responsible for major design decisions, and mostly just implemented as the client wanted. "

    return (
      <Layout location={this.props.location} title={siteTitle}>
        <SEO
          title="All posts"
          keywords={[`blog`, `gatsby`, `javascript`, `react`, "resume"]}
        />

        {/* <Bio /> */}
        <div className={"row"} style={{ marginLeft: rhythm(1) }}>
          <span style={{ marginTop: rhythm(1) }}>
            <h4>Projects and Work</h4>
          </span>
          <ProjectCard
            name={"Finance App"}
            description={financeDescription}
            link={"/finance-app/"}
            id={"test"}
          />
          <ProjectCard
            name={"Static Website"}
            description={surv}
            link={"https://surveillancesolutions.org"}
          />
        </div>
        <button onClick={this.toggleJobs}>
          {showJobs ? "Hide Resume" : "View Resume"}
        </button>
        {/* Conditionally Render Jobs and update className for fade in animation */}
        <div className={className}>{showJobs && <Jobs />}</div>
        {posts.map(({ node }) => {
          const title = node.frontmatter.title || node.fields.slug
          return (
            <div key={node.fields.slug}>
              <h3
                style={{
                  marginBottom: rhythm(1 / 4),
                }}
              >
                <Link style={{ boxShadow: `none` }} to={node.fields.slug}>
                  {title}
                </Link>
              </h3>
              <small>{node.frontmatter.date}</small>
              <p
                dangerouslySetInnerHTML={{
                  __html: node.frontmatter.description || node.excerpt,
                }}
              />
            </div>
          )
        })}
      </Layout>
    )
  }
}

export default BlogIndex

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(
      sort: { fields: [frontmatter___date], order: DESC }
      filter: { frontmatter: { published: { eq: true } } }
    ) {
      edges {
        node {
          excerpt
          fields {
            slug
          }
          frontmatter {
            date(formatString: "MMMM DD, YYYY")
            title
            description
            published
          }
        }
      }
    }
  }
`
